import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams,ToastController} from 'ionic-angular';
import {AngularFireAuth} from 'angularfire2/auth';
import {AngularFireDatabase, FirebaseObjectObservable,FirebaseListObservable} from 'angularfire2/database'



@IonicPage()
@Component({
  selector: 'page-rating',
  templateUrl: 'rating.html',
})
export class RatingPage {
  review: any = {
    rating: '',
    comment: ''
  }
  itemId: '';
  index: '';
  orderId: '';
  reviews: any[] = [];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public af: AngularFireAuth,
              public db: AngularFireDatabase,
              public toastCtrl:ToastController) {

         this.itemId = this.navParams.get('itemId');
         this.index = this.navParams.get('index');
         this.orderId = this.navParams.get('orderId');

    this.db.list('/menuItems/' + this.itemId + '/reviews').subscribe(
      res => {
        this.reviews = res;
      })

  }

  onSubmit() {
    console.log('review-' + JSON.stringify(this.review));
      if (this.af.auth.currentUser) {
        this.review.userId = this.af.auth.currentUser.uid;
        if (this.reviews.length == 0) {
          this.db.object('/menuItems/' + this.itemId + '/reviews/' + 0).update({
            rating: this.review.rating,
            comment: this.review.comment,
            userId: this.review.userId
          }).then((response) => {
            //console.log("update-"+JSON.stringify(response));
          });
        }
        else {
          this.db.object('/menuItems/' + this.itemId + '/reviews/' + this.reviews.length).update({
            rating: this.review.rating,
            comment: this.review.comment,
            userId: this.review.userId
          }).then((response) => {
            //console.log("update--"+JSON.stringify(response));
          });
        }

        this.db.object('/orders/' + this.orderId + '/cart/' + this.index + '/item/' + '/review').update(this.review).then((success) => {
          this.createToaster('Review submitted successfully', '3000');
          this.review = {};
          this.navCtrl.push("OrderDetailsPage",{
            orderId: this.orderId,
            index: this.index
          })
        });

      }
  }
   createToaster(message, duration) {
        let toast = this.toastCtrl.create({
            message: message,
            duration: duration
        });
        toast.present();
    }

}



